import React from "react";
import styles from "./style.css";
import { baseURL, products } from "../../../api";
import Header from "../../../components/header";
import Loader from "../../../components/loaders/loaders";
import { HeadingMed } from "../../../components/headings";
import OrderBlock from "../../../containers/orders-block";
import Card from "@material-ui/core/Card";
import { connect } from "react-redux";
import fire from '../../../fire'
// import { RoundButton } from "../../../components/button";
import { images } from "../../../constants";
import { loadUser } from "../../../actions";
import { ButtonTwo } from "../../components/button";

class Page extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        products: [],
        activeBlock: "",
    };

    async componentDidMount() {
        let route = this.props.match.params.route;
        this.setState({ loading: false, activeBlock: route });
    }
    componentDidUpdate() {
        let route = this.props.match.params.route;
        if (route !== this.state.activeBlock) {
            this.setState({ activeBlock: route });
        }
    }

    routeHandler = (route) => {
        this.props.history.push("/account/orders");
    };
    logoutHandler = () => {
        this.setState({ loading : true})
        localStorage.removeItem('token');
        fire.auth().signOut();
        this.props.loadUser({user : {}, token : ''})
        setTimeout(() => {
            this.setState({ loading: false });
            this.props.history.push('/login')
        },500)
    }

    render() {
        let user = this.props.user;
        let address = user.address ? user.address[user.currentAddress] : {};
        address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}, ${address.landmark}`;
        let activeBlock = this.state.activeBlock;
        let block =
            activeBlock === "orders" ? (
                <OrderBlock
                    navigation={{
                        history: this.props.history,
                        match: this.props.match,
                    }}
                />
            ) : null;

        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}
                {this.props.user.name && this.props.token ? (
                    <div className={styles.box}>
                        <div>
                            <div>
                                <div className={styles.accountContainer}>
                                    <div className={styles.card}>
                                        <h6>Account Details</h6>
                                        <div>
                                            <img
                                                src={images.GET_PF_BG}
                                                className={styles.cardImg}
                                            />
                                        </div>
                                        <div style={{ flex: 1 }}>
                                            <div
                                                className={
                                                    styles.displayFlexRow
                                                }
                                            >
                                                <img
                                                    src={
                                                        baseURL +
                                                        "/images/" +
                                                        "user.png"
                                                    }
                                                    className={styles.userImg}
                                                />
                                                <ButtonTwo
                                                    title="Logout"
                                                    onClick={this.logoutHandler}
                                                />
                                            </div>
                                            <h6>{user.name}</h6>
                                            <p>{user.phoneNumber}</p>
                                            <p>{address}</p>
                                            {/* <RoundButton title="Edit Profile" /> */}
                                        </div>
                                    </div>

                                    <div className={styles.card}>
                                        <h6>Orders and Subscription</h6>
                                        <img
                                            src={images.GET_APP_BG}
                                            className={styles.cardImg}
                                        />
                                        <div style={{ width: "30%" }}>
                                            <p
                                                className={styles.borderText}
                                                onClick={() =>
                                                    this.routeHandler("orders")
                                                }
                                            >
                                                Your Orders
                                            </p>
                                            <p className={styles.borderText}>
                                                Subscribed Shops
                                            </p>
                                            <p className={styles.borderText}>
                                                Shops Near You
                                            </p>
                                            <p className={styles.borderText}>
                                                Notifications
                                            </p>
                                            <p className={styles.text}>
                                                Offers and Deals
                                            </p>
                                        </div>
                                    </div>

                                    <div className={styles.card}>
                                        <h6>General Setting</h6>
                                        <img
                                            src={images.BG_1}
                                            className={styles.cardImg}
                                        />
                                        <div>
                                            <p className={styles.borderText}>
                                                Manage Pro Subscription
                                            </p>
                                            <p className={styles.borderText}>
                                                Review your purchase
                                            </p>
                                            <p className={styles.borderText}>
                                                Terms and Conditions
                                            </p>
                                            <p className={styles.borderText}>
                                                Help Center
                                            </p>
                                            <p className={styles.text}>
                                                SMS alert prefrences
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : (
                    <div>
                        <p>Please Login</p>
                    </div>
                )}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return state;
};
export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Page);
