import React, { useEffect, useState } from 'react';
import Icon from '../../components/icons';
import styles from './style.css';


const Rating = props => {

    const [color, changeColor] = useState("#e0e0e0");

    
    useEffect(() => {
        let rating = props.rating;
        if (rating === 0) {
            changeColor("40cf4e");
        }else if (rating <=1) {
            changeColor("#e0e0e0");
        } else if (rating <= 2) {
            changeColor("#d12e2e");
        } else if (rating <= 3) {
            changeColor("#f5ab3d");
        } else if (rating <= 4) {
            changeColor("#f5dc3d");
        } else if (rating <= 5) {
            changeColor("#91cf40");
        } else {
            changeColor("#40cf4e");
        }
    }, [props.rating]);

    return (
        <div style={{backgroundColor : color}} className={styles.rating}>
            <Icon name="star" size="2rem" color="#fff" />
            <span>{"  "}{props.rating}</span>
        </div>
    )
}

export default Rating