import React, { useState } from "react";
import { } from "react-router-dom";
import {colors} from '../../../constants'
// import { connect } from "react-redux";
import sprite from '../../../assets/icons/sprite.svg';
const Icon = (props) => {
    return (
        <svg
            style={{
                width: props.size ? props.size : "26px",
                height: props.size ? props.size : "26px",
                cursor: "pointer",
                margin : props.gap ? "0 1rem" : "0"
            }}
            onClick = {props.onClick}
        >
            <use
                xlinkHref={`${sprite}#icon-${props.name}`}
                style={{ fill: props.color ? props.color : colors.AQUA_BLUE }}
            ></use>
        </svg>
    );
};


export default Icon;
