import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import classes from "./style.css";
import { connect } from "react-redux";
import { images } from "../../../constants";
import Icon from "../../components/icons";
import { HeadingMed } from "../../components/headings";

const Routes = (props) => {
    const [status, changeStatus] = useState('fail')
    useEffect(() => {
        let params = props.match.params;
        changeStatus(params.status);
    },[props.match])
    return (
        <div className={classes.container}>
            <div className={classes.box}>
                {status === "success" ? (
                    <div className={classes.rightBox}>
                        <h2>CONGRATULATIONS !!</h2>
                        <h3>Your order has been placed.</h3>
                        <h3>We'll reach out you shortly with your order.</h3>
                        <div className={classes.center}>
                            <Link to="/orders">Go Back to Orders</Link>
                        </div>
                    </div>
                ) : (
                    <div className={classes.rightBox}>
                        <h2>SORRY YOUR TRANSACTION FAILED !!</h2>
                    </div>
                )}
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps)(Routes);
