import React from "react";
import styles from "./style.css";
import { baseURL, products } from "../../../api";
import Loader from "../../components/loaders/spinner";
import Rating from "../../components/rating-box";
import {
    HeadingMed,
    HeadingReg,
    HeadingSmall,
} from "../../../components/headings";
import call from "../../../api/call";
// import Rating from "../../../components/rating-box";
import { connect } from "react-redux";
import { loadUser } from "../../../actions";
let ratings = [
    "1 and Above",
    "2 and Above",
    "3 and Above",
    "4 and Above",
    "5 Stars",
];
class Page extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        sellerCartId: "",
        product: { details: {}, productRating: { rating: 0, count: 1 } },
        images: [],
        activeImage: 0,
        quantity: 1,
    };

    async componentDidMount() {
        let sellerCartId = new URLSearchParams(this.props.location.search).get(
            "sellerCartId"
        );
        this.setState({ sellerCartId, loading: false });
        await this.getProduct();
        // alert(sellerCartId)
    }

    componentDidUpdate() {
        let name = new URLSearchParams(this.props.location.search).get(
            "sellerCartId"
        );
        if (name !== this.state.sellerCartId) {
            this.getProduct();
        }
    }

    getProduct = async () => {
        this.setState({ loading: true });
        try {
            let sellerCartId = new URLSearchParams(
                this.props.location.search
            ).get("sellerCartId");
            let result = await call.user(
                "/products/seller-cart/id?sellerCartId=" + sellerCartId,
                {}
            );
            let images = result.data.product.images;
            this.setState({
                product: result.data.product,
                sellerCartId,
                loading: false,
                images,
            });
            console.log(result.data.product);
        } catch (error) {
            this.setState({ loading: false });
        }
    };

    addToCartHandler = async () => {
        this.setState({ loading: true });
        let data = {
            sellerCartId: this.state.sellerCartId,
            quantity: this.state.quantity,
        };
        try {
            let result = call.user("/users/cart/add-product", {
                data: data,
                headers: {
                    authorization: this.props.token,
                },
            });
            let user = await call.user("/users", {
                headers: {
                    authorization: this.props.token,
                },
            });
            console.log("user   ---- ",user);
            this.props.loadUser({ user: user.data.user });
            this.setState({ loading: false });
            alert("congratulations, product added to you cart");
        } catch (error) {
            alert(error);
            console.log(error);
            this.setState({ loading: false });
        }
    };
    buyNowHandler = () => {
        let products = [];
        products.push({ ...this.state.product,quantity : this.state.quantity });
        this.props.history.push({
            pathname: '/confirm-order', state: {
            products
        }});
    }

    render() {
        let imagesBlock = this.state.images.map((image, index) => {
            if (index < 5)
                return (
                    <div
                        className={styles.sideImage}
                        onMouseOver={() => {
                            this.setState({ activeImage: index });
                        }}
                        onClick={() => {
                            this.setState({ activeImage: index });
                        }}
                    >
                        <img src={baseURL + "/images/" + image} />
                    </div>
                );
        });
        let product = this.state.product;
        // console.log(product);
        let nutBlock = product.details.nutritional_information
            ? Object.keys(product.details.nutritional_information).map(
                  (value, index) => {
                      let data = product.details.nutritional_information;
                      return (
                          <tr key={index}>
                              <th>{value}</th>
                              <td>{data[value]}</td>
                          </tr>
                      );
                  }
              )
            : null;
        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}
                <div className={styles.block}>
                    <div className={styles.leftBlock}>
                        <div className={styles.displayFlexRow}>
                            <div style={{ flex: 1 }}>{imagesBlock}</div>
                            <div
                                style={{ flex: 4 }}
                                className={styles.fullImage}
                            >
                                <img
                                    src={
                                        baseURL +
                                        "/images/" +
                                        this.state.images[
                                            this.state.activeImage
                                        ]
                                    }
                                />
                            </div>
                        </div>

                        <div
                            className={styles.displayFlexRow}
                            style={{ padding: "10px" }}
                        >
                            <button
                                className={styles.addToCart}
                                onClick={this.addToCartHandler}
                            >
                                Add to Cart
                            </button>
                            <button className={styles.buyNow} onClick= {this.buyNowHandler}>Buy Now</button>
                        </div>
                    </div>
                    <div className={styles.rightBlock}>
                        <div className={styles.displayFlexRow}>
                            <div>
                                <HeadingMed title={product.name} />
                               {product.sellerId ? <h6>Seller {product.sellerId.shopName}</h6> : null}
                                <div>
                                    <span className={styles.amount}>
                                        Rs {product.amount}
                                    </span>
                                    <span className={styles.mrp}>
                                        {product.mrp}
                                    </span>
                                    <span className={styles.discount}>
                                        {(
                                            (product.discount / product.mrp) *
                                            100
                                        ).toFixed(2)}{" "}
                                        % Off
                                    </span>
                                </div>
                                <Rating
                                    rating={(
                                        product.productRating.rating /
                                        (!product.productRating.count
                                            ? 1
                                            : product.productRating.count)
                                    ).toFixed(1)}
                                />
                            </div>
                            <div>
                                <div>
                                    <span
                                        className={styles.button}
                                        onClick={() => {
                                            if (this.state.quantity > 1) {
                                                this.setState({
                                                    quantity:
                                                        this.state.quantity - 1,
                                                });
                                            }
                                        }}
                                    >
                                        -
                                    </span>
                                    <span className={styles.button}>
                                        {this.state.quantity}
                                    </span>
                                    <span
                                        className={styles.button}
                                        onClick={() => {
                                            if (
                                                this.state.quantity <
                                                this.state.product.quantity
                                            ) {
                                                this.setState({
                                                    quantity:
                                                        this.state.quantity + 1,
                                                });
                                            }
                                        }}
                                    >
                                        +
                                    </span>
                                    <div>
                                        {this.state.quantity <=
                                        this.state.product.quantity ? (
                                            <p className={styles.greenText}>
                                                In Stock
                                            </p>
                                        ) : (
                                            <p className={styles.redText}>
                                                Out of Stock
                                            </p>
                                        )}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <HeadingMed title="Product Description" />
                        <p>{product.description}</p>

                        <div>
                            <HeadingReg title="Nutrition Value" />
                            <table>{nutBlock}</table>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Page);
