import React from "react";
import styles from "./style.css";
import { products } from "../../../api";
import Loader from "../../components/loaders/spinner";
import { HeadingReg, HeadingSmall } from "../../components/headings";
import Slider from "@material-ui/core/Slider";
// import Checkbox from "@material-ui/core/Checkbox";
let ratings = [
    "1 and Above",
    "2 and Above",
    "3 and Above",
    "4 and Above",
    "5 Stars",
];
class Page extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        products: [],
        priceRange: [100, 2000],
        shopRange: [0, 2],
        brand: [],
        brandValue: "",
        ratingValue: "",
    };

    async componentDidMount() {
        await this.searchProduct();
    }

    componentDidUpdate() {
        let name = new URLSearchParams(this.props.location.search).get("name");
        if (name !== this.state.searchValue) {
            this.searchProduct();
        }
    }

    searchProduct = async () => {
        this.setState({ loading: true });
        let name = new URLSearchParams(this.props.location.search).get("name");
        let result = await products.getProducts(name);
        let product = result.list;
        let tempSet = new Set();
        let brand = [];
        product.forEach((value, index) => {
            if (
                value.details.manufacturer &&
                !tempSet.has(value.details.manufacturer)
            ) {
                tempSet.add(value.details.manufacturer);
                brand.push(value.details.manufacturer);
            }
        });
        this.setState({
            products: product,
            searchValue: name,
            loading: false,
            brand,
        });
    };

    render() {
        let productList = this.state.products.map((value, index) => {
            return (
                <div
                    className={styles.itemBlock}
                    onClick={() => {
                        this.props.history.push(
                            "/product?sellerCartId=" + value._id
                        );
                    }}
                >
                    <img
                        src={"http://localhost:3000/images/" + value.images[0]}
                    />
                    <h3>{value.name}</h3>
                    <p>{value.sellerId.shopName}</p>
                    <div
                        className={styles.displayFlexRow}
                        style={{ justifyContent: "flex-start" }}
                    >
                        <h4>Rs {value.amount}</h4>
                        <h4>
                            {"  "}
                            <stike>{value.mrp}</stike>
                        </h4>
                        <h4 style={{ color: "green" }}>
                            {"   "}
                            {((value.discount / value.mrp) * 100).toFixed(2)}%
                            off
                        </h4>
                    </div>
                </div>
            );
        });
        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}

                <div
                    className={styles.displayFlexRow}
                    style={{ width: "100%", alignItems : "flex-start"}}
                >
                    <div className={styles.filterBlock}>
                        <HeadingReg title="Filter" />
                        <div className={styles.filterBox}>
                            <div style={{ margin: "20px 0" }}>
                                <HeadingReg title="Price Range" />
                                <Slider
                                    value={this.state.priceRange}
                                    onChange={(event, newValue) => {
                                        if (newValue[1] - newValue[0] >= 100)
                                            this.setState({
                                                priceRange: newValue,
                                            });
                                    }}
                                    aria-labelledby="range-slider"
                                    valueLabelDisplay="auto"
                                    min={100}
                                    max={10000}
                                    step={100}
                                />
                            </div>
                            <div style={{ margin: "20px 0" }}>
                                <HeadingReg title="Shop Range" />
                                <Slider
                                    value={this.state.shopRange}
                                    onChange={(event, newValue) => {
                                        if (newValue[1] - newValue[0] >= 1)
                                            this.setState({
                                                shopRange: newValue,
                                            });
                                    }}
                                    aria-labelledby="range-slider"
                                    valueLabelDisplay="auto"
                                    min={0}
                                    max={5}
                                    step={0.2}
                                />
                            </div>
                            <div style={{ margin: "20px 0" }}>
                                <HeadingReg title="Brand or Manufacturer" />
                                {this.state.brand.map((brand, index) => {
                                    return (
                                        <div
                                            className={`${styles.displayFlexRow} ${styles.borderBox}`}
                                        >
                                            <HeadingSmall title={brand} />
                                            <input
                                                type="checkbox"
                                                checked={
                                                    brand ===
                                                    this.state.brandValue
                                                }
                                                onClick={() => {
                                                    this.setState({
                                                        brandValue: brand,
                                                    });
                                                }}
                                                className={styles.checkBox}
                                            />
                                        </div>
                                    );
                                })}
                            </div>
                            <div style={{ margin: "0px 0" }}>
                                <HeadingReg title="Customer Rating" />
                                {ratings.map((brand, index) => {
                                    return (
                                        <div
                                            className={`${styles.displayFlexRow} ${styles.borderBox}`}
                                        >
                                            <HeadingSmall title={brand} />
                                            <input
                                                type="checkbox"
                                                checked={
                                                    brand ===
                                                    this.state.brandValue
                                                }
                                                onClick={() => {
                                                    this.setState({
                                                        ratingValue: brand,
                                                    });
                                                }}
                                                className={styles.checkBox}
                                            />
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                    <div style={{ width: "100%", height: "100%", padding: "4px"}}>
                        <p style={{ paddingLeft: "10px" }}>
                            For your search {this.state.products.length} items
                            found.
                        </p>
                        <div className={styles.productBlock}>{productList}</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Page;
