import call from './call';

export const getProducts = async (search) => {
    try {
        let resultt = await call.instance({
            method : 'GET',
            url : '/products/seller-products?s=' + search,
        });
        return resultt.data
    } catch (error) {
        console.log(error);
        return new Error('SERVER_ERROR');
    }
}



