import React from "react";
import styles from "./style.css";
// import Loader from "../../components/loaders/loaders";
import {
    HeadingMed,
    HeadingReg,
    HeadingSmall,
} from "../../components/headings";
import call from "../../api/call";
import { connect } from "react-redux";
import Loader from "../../components/loaders/spinner";
import { baseURL } from "../../api";
import { Button } from "@material-ui/core";
import { RoundButton } from "../../components/button";
class Block extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        orders: [],
        order: {},
    };

    async componentDidMount() {
        console.log(this.props.params);
        this.setState({ loading: false });
        await this.getOrders();
    }

    getOrders = async () => {
        this.setState({ loading: true });
        let orderId = this.props.params.orderId;
        try {
            let order = await call.user("/users/order?id=" + orderId, {
                headers: {
                    authorization: this.props.token,
                },
            });
            let result = await call.user(
                "/users/order/products?orderId=" + orderId,
                {
                    headers: {
                        authorization: this.props.token,
                    },
                }
            );
            console.log("------------------>", result.data);
            this.mapOrders(result.data.orderProducts, {});
            this.setState({ loading: false, order: order.data.order });
        } catch (error) {
            this.setState({ loading: false });
        }
    };

    mapOrders = (orders, order) => {
        let products = orders;
        let tempSet = new Set();
        let list = {};
        products.forEach((value, index) => {
            if (!tempSet.has(value.sellerId)) {
                list[value.sellerId] = {
                    sellerId: value.sellerId,
                    shopName: value.shopName,
                    shopAddress: value.shopAddress,
                    sellerPhoneNumber: value.sellerPhoneNumber,
                    products: [],
                };
                tempSet.add(value.sellerId);
            }

            list[value.sellerId].products.push(value);
        });
        this.setState({ order, orders: list });
    };

    render() {
        let orderBlock = Object.values(this.state.orders).map(
            (value, index) => {
                let address = value.shopAddress;
                let ad = address
                    ? `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`
                    : "";
                // if (address) {
                //     address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`;
                // }
                return (
                    <div className={styles.borderBox}>
                        <HeadingReg
                            title={value.shopName + "  #" + value.sellerId}
                        />
                        <br></br>
                        <div className={styles.displayFlexRow}>
                            <p>Shop Phone Number {value.sellerPhoneNumber}</p>
                            <p>Shop Address {ad}</p>
                        </div>
                        <br></br>
                        {value.products.map((product, index) => {
                            return (
                                <div className={styles.displayFlexRow}>
                                    <div style={{ width: "20rem" }}>
                                        <img
                                            src={
                                                baseURL +
                                                "/images/" +
                                                product.image
                                            }
                                            width="90px"
                                        />
                                    </div>
                                    <div style={{ flex: 2 }}>
                                        <div>
                                            <HeadingReg
                                                title={
                                                    "Tracking ID #" +
                                                    product._id
                                                }
                                            />
                                            <HeadingSmall
                                                title={product.name}
                                            />
                                            <HeadingSmall
                                                title={`Rs ${product.amount} ( Rs ${product.mrp} per unit)`}
                                            />
                                            <HeadingSmall
                                                title={`Quantity ${product.quantity} `}
                                            />
                                        </div>
                                    </div>
                                    <div style={{ flex: 2 }}>
                                        <RoundButton title={product.status} />
                                        <RoundButton title="Track Your Product" />
                                    
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                );
            }
        );
        let order = this.state.order;
        let address = '';
        if (order.receiverAddress) {
            address = order.receiverAddress;
            address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`;
        }
        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}
                {/* <HeadingMed title="Your Orders" /> */}
                <div className={styles.borderBox}>
                    <HeadingReg title="Delivery Details" />
                    <div className={styles.displayFlexRow}>
                        <div>
                            <HeadingSmall
                                title={"Receiver Name - " + order.receiverName}
                            />
                            <HeadingSmall
                                title={"Delivery Address - " + address}
                            />
                            <HeadingSmall
                                title={
                                    "Receiver Phone Number - " +
                                    order.receiverPhoneNumber
                                }
                            />
                        </div>
                        <div style={{ alignSelf: "flex-end" }}>
                            <HeadingSmall
                                title={"Payment Method - " + order.method}
                            />
                            {
                                order.transactionId ? <HeadingSmall
                                    title={"Transaction ID  #" + order.transactionId}
                                /> : null
                            }
                            <HeadingSmall
                                title={
                                    "Payment Status - " + order.paymentStatus
                                        ? "Paid"
                                        : "Not Paid"
                                }
                            />
                            <HeadingSmall
                                title={
                                    "Total Amount - Rs " +
                                    order.amount
                                }
                            />
                        </div>
                    </div>
                </div>
                <div className={styles.shopBlock}>{orderBlock}</div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps)(Block);
