import React, { useState } from 'react';
import { Route, Switch } from 'react-router-dom'
import { connect } from 'react-redux'
import GuardRoute from '../components/guarded-route/GuardRoute';
import HomePage from './home';
import AccountPage from './account'
import NotificationsPage from './notifications';
import OrdersPage from './orders';
import ShopPage from './shops';
import OfferPage from './offers';
import LoginPage from './login'
import CartPage from './cart';
import RegisterPage from './register'
import SearchPage from './search';
import ProductPage from './product'
import fire from '../../fire'
import ConfirmOrderPage from './confirm-order';
import OrderSuccessPage from './order-success';
const Routes = props => {
    
    const [loggedIn, changeUser] = useState(false);
    const { user , token} = props;
    console.log(props);
    return (
        <Switch>
            <GuardRoute
                exact={true}
                path="/account"
                component={AccountPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/orders"
                component={OrdersPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/cart"
                component={CartPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/offers"
                component={OfferPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/shops"
                component={ShopPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/notifications"
                component={NotificationsPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/register"
                component={RegisterPage}
                valid={fire.auth().currentUser && fire.auth().currentUser.uid}
            />
            <GuardRoute
                exact={true}
                path="/confirm-order"
                component={ConfirmOrderPage}
                valid={user && token}
            />
            <GuardRoute
                exact={true}
                path="/payment/:status"
                component={OrderSuccessPage}
                valid={user && token}
            />

            <Route path="/login" component={LoginPage} exact />
            <Route path="/search" component={SearchPage} exact />
            <Route path="/product" component={ProductPage} exact />
            <Route path="/" component={HomePage} exact />
            <Route render={() => <div>Page not found 404</div>} />
        </Switch>
    );
}

const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps)(Routes);