import React from "react";
import styles from "./style.css";
// import Loader from "../../components/loaders/loaders";
import {
    HeadingMed,
    HeadingReg,
    HeadingSmall,
} from "../../components/headings";
import call from "../../../api/call";
import { connect } from "react-redux";
import Loader from "../../components/loaders/spinner";
import OrderDetailsBlock from "./orders-details-block";
import Icon from "../../components/icons";
import QRCode from 'qrcode.react'
class Block extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        orders: [],
        active: "orders",
        activeParams: {},
        heading: "Your Orders",
    };
    async componentDidUpdate() {
        let orderId = new URLSearchParams(
            this.props.history.location.search
        ).get("orderId");
        let productId = new URLSearchParams(
            this.props.history.location.search
        ).get("productId");
        if (
            this.state.activeParams.orderId !== orderId ||
            this.state.activeParams.productId !== productId
        ) {
            let activeParams = {
                orderId,
                productId,
            };
            let heading = this.state.heading;
            if (orderId) {
                heading += " > " + orderId;
            }
            if (productId) {
                heading += " > " + productId;
            }
            this.setState({
                loading: false,
                active: orderId ? "products" : "orders",
                activeParams,
                heading,
            });
            await this.getOrders();
        }
    }

    async componentDidMount() {
        if (this.props.history.location.search) {
            let activeParams = {};
            let orderId = new URLSearchParams(
                this.props.history.location.search
            ).get("orderId");
            let productId = new URLSearchParams(
                this.props.history.location.search
            ).get("productId");
            activeParams = {
                orderId,
                productId,
            };
            let heading = this.state.heading;
            if (orderId) {
                heading += " > " + orderId;
            }
            if (productId) {
                heading += " > " + productId;
            }
            this.setState({
                loading: false,
                active: "products",
                activeParams,
                heading,
            });
        } else {
            this.setState({ loading: false });
        }
        await this.getOrders();
    }

    getOrders = async () => {
        this.setState({ loading: true });
        try {
            let result = await call.user("/users/order", {
                headers: {
                    authorization: this.props.token,
                },
            });
            this.setState({
                loading: false,
                orders: result.data.orders.reverse(),
            });
        } catch (error) {
            console.log("Error--", error);
            this.setState({ loading: false });
        }
    };

    render() {
        let orderBlock = this.state.orders.map((value, index) => {
            let address = value.receiverAddress;
            address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`;
            return (
                <div
                    className={styles.itemBlock}
                    onClick={() => {
                        // this.setState({
                        //     active: "products",
                        //     activeParams: { orderId : value.orderId },
                        // });
                        this.props.history.push("?orderId=" + value.orderId);
                    }}
                >
                    <HeadingReg
                        title={`${index + 1}. Order Id #${value.orderId}`}
                    />
                    <HeadingSmall
                        title={new Date(
                            parseInt(value.orderId)
                        ).toLocaleString()}
                    />
                    <HeadingSmall title={value.receiverName} />
                    <HeadingSmall title={address} />
                    <HeadingSmall title={`Rs ${value.amount}`} />
                    {/* <p>{value.receiverName}</p>
                    <p>{address}</p>
                    <p>Items #{value.products.length}</p>
                    <h3>Rs {value.amount}</h3> */}
                </div>
            );
        });

        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}
                <div className={styles.displayFlexRow}>
                    <HeadingMed title={this.state.heading} />
                    <div>
                        <Icon name="filter"/>
                    </div>
                </div>
                {this.state.active === "orders" ? (
                    <div className={styles.orderBlock}>{orderBlock}</div>
                ) : (
                    <OrderDetailsBlock params={this.state.activeParams} />
                )}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps)(Block);
