import React, { useEffect, useState } from "react";
import classes from './style.css';
import { Switch, BrowserRouter, Router, Route } from "react-router-dom";
import Routes from "./routes";
import { createBrowserHistory } from "history";
import Loader from './components/loaders/loaders'
// import { Router } from "react-router";
import HomeNavigator from "./components/home-navigator";
import Header from "./components/header";
import call from "../api/call";
import fire from '../fire'
import { connect } from "react-redux";
import { loadUser } from "../actions";
// import {div} from '@material-ui/core'
const App = (props) => {

    const [loading, changeLoading] = useState(true);
    useEffect(async() => {
        let token = localStorage.getItem("token");
        // let currentUser = fire.auth().currentUser;
       console.log("Token", token);
        if (token) {
            try {
                let result = await call.user('/users', {
                    headers: {
                       authorization : token
                   }
                })
                props.loadUser({
                    user: result.data.user,
                    token
                })
                changeLoading(false);
            } catch (error) {
                console.log(error);
                changeLoading(false)
            }
        } else {
            changeLoading(false)
        }
    },[])
    return loading ? (
        <Loader />
    ) : (
        <>
            <div className={classes.container}>
                <div className={classes.navigator}>
                    <HomeNavigator />
                </div>
                <div item className={classes.header}>
                    <Header />
                </div>
                <div item className={classes.body}>
                    <Switch>
                        <Route component={Routes} />
                    </Switch>
                </div>
            </div>
        </>
    );
};

const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps, {
    loadUser : loadUser
})(App);
