import React, { useState, useRef, useEffect } from "react";
import classes from './style.css';
import {useHistory,withRouter} from "react-router-dom";
import { connect } from "react-redux";
import { Grid } from "@material-ui/core";
import Icon from '../../components/icons';
import { colors } from "../../../constants";
const Routes = (props) => {
    const [screen, handleResize] = useState({
        width: window.innerWidth,
        height: window.innerHeight,
        size : "30px"
    });
    const [active , changeActive] = useState('')
    let history = useHistory();
    // console.log(history);
    useEffect(() => {
        window.addEventListener("resize", () =>
            handleResize({
                width: window.innerWidth,
                height: window.innerHeight,
                size : window.innerWidth > 480 ? "25px" : "30px"
            })
        );
        changeActive(history.location.pathname.split('/')[1])
    }, []);

    const iconClickHandler = (name) => {
        props.history.push("/" + name);
        changeActive(name);
    }
    if (!props.token) {
        return <></>
    }
    return (
        <Grid
            container
            direction={screen.width <= 480 ? "row" : "column"}
            justify="space-evenly"
            alignItems="center"
            className={classes.container}
        >
            <Icon
                name="gift"
                size={screen.size}
                onClick={() => iconClickHandler("offers")}
                color={
                    active === "offers" ? colors.CORAL_RED : colors.AQUA_BLUE
                }
            />
            <Icon
                name="box"
                size={screen.size}
                onClick={() => iconClickHandler("orders")}
                color={
                    active === "orders" ? colors.CORAL_RED : colors.AQUA_BLUE
                }
            />
            <Icon
                name="home"
                size={screen.size}
                onClick={() => iconClickHandler("")}
                color={
                    active === "" ? colors.CORAL_RED : colors.AQUA_BLUE
                }
            />
            <Icon
                name="shop"
                size={screen.size}
                onClick={() => iconClickHandler("shop")}
                color={
                    active === "shop" ? colors.CORAL_RED : colors.AQUA_BLUE
                }
            />
            <Icon
                name="user"
                size={screen.size}
                onClick={() => iconClickHandler("account")}
                color={
                    active === "account" ? colors.CORAL_RED : colors.AQUA_BLUE
                }
            />
        </Grid>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default withRouter(connect(mapStateToProps)(Routes));
