import React, { useState, useRef, useEffect } from "react";
import classes from "./style.css";
import { useHistory, withRouter, useParams } from "react-router-dom";
import { connect } from "react-redux";
import { Grid, TextField, Button } from "@material-ui/core";
import Icon from "../../components/icons";
import { categories, colors, images } from "../../../constants";
const Header = (props) => {
    const [screen, handleResize] = useState({
        width: window.innerWidth,
        height: window.innerHeight,
        size: "30px",
    });
    const [searchValue, changeValue] = useState("");
    const [active, changeActive] = useState("");
    const [cart, changeCart] = useState(0);
    const [notification, changeNotification] = useState(0);
    let history = useHistory();
    // console.log(history);
    useEffect(() => {
        window.addEventListener("resize", () =>
            handleResize({
                width: window.innerWidth,
                height: window.innerHeight,
                size: window.innerWidth > 480 ? "25px" : "20px",
            })
        );
        changeValue(new URLSearchParams(history.location.search).get("name"));
        changeActive(history.location.pathname.split("/")[1]);
    }, []);
    useEffect(() => {
        if (props.user && props.user.userCartItems) {
            changeCart(props.user.userCartItems.length);
        }
        if (props.user && props.user.notifications) {
            changeNotification(props.user.notifications.length);
        }
    }, [props.user]);
    let categoriesBlock = categories.map((value, index) => {
        return (
            <div
                className={`${classes.displayFlexCol} ${classes.categoriesItem}`}
            >
                <img src={images["CT_" + index]} />
                <li>{value}</li>
            </div>
        );
    });

    return (
        <div className={classes.container}>
            <Grid
                container
                direction="row"
                justify="space-between"
                alignItems="center"
            >
                <img src={images.logo} />

                <Grid
                    container
                    direction="row"
                    alignItems="center"
                    justify="center"
                    className={classes.input}
                >
                    <input
                        type="text"
                        className={classes.input}
                        placeholder="Search Product"
                        value={searchValue}
                        onKeyPress={({ code }) => {
                            if (code === "Enter") {
                                props.history.push(
                                    "/search?name=" + searchValue
                                );
                            }
                        }}
                        onChange={({ target }) => changeValue(target.value)}
                    />
                    <span>
                        <Icon
                            name="search"
                            size={screen.size}
                            // onClick={() => iconClickHandler("offers")}
                            color={
                                active === "offers"
                                    ? colors.CORAL_RED
                                    : colors.AQUA_BLUE
                            }
                        />
                    </span>
                </Grid>
                <Grid
                    container
                    direction="row"
                    alignItems="center"
                    justify="flex-end"
                    className={classes.rightBox}
                >
                    {!props.token ? (
                        <button
                            className={classes.roundButton}
                            onClick={() => {
                                props.history.push("/login");
                            }}
                        >
                            Login
                        </button>
                    ) : null}
                    <div className={classes.iconBox}>
                        <Icon
                            name="bell"
                            size={screen.size}
                            // onClick={() => iconClickHandler("offers")}
                            color={
                                active === "offers"
                                    ? colors.CORAL_RED
                                    : colors.AQUA_BLUE
                            }
                            gap={true}
                        />
                        {notification ? <span>{notification}</span> : null}
                    </div>
                    <div className={classes.iconBox} onClick={() => {
                        history.push('/cart')
                    }}>
                        <Icon
                            name="shopping-cart"
                            size={screen.size}
                            // onClick={() => iconClickHandler("offers")}
                            color={
                                active === "offers"
                                    ? colors.CORAL_RED
                                    : colors.AQUA_BLUE
                            }
                            gap={true}
                        />
                        {cart ? <span>{cart}</span> : null}
                    </div>
                </Grid>
            </Grid>
            {!props.hide ? (
                <div className={classes.categoriesBox}>
                    <ul className={classes.categoriesList}>
                        {categoriesBlock}
                    </ul>
                </div>
            ) : null}
        </div>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default withRouter(connect(mapStateToProps)(Header));
