import React from "react";
import styles from "./style.css";
// import Loader from "../../components/loaders/loaders";
import {
    HeadingMed,
    HeadingReg,
    HeadingSmall,
} from "../../components/headings";
import call from "../../../api/call";
import { connect } from "react-redux";
import Loader from "../../components/loaders/spinner";
import { baseURL } from "../../../api";
import { Button } from "@material-ui/core";
import { ButtonTwo } from "../../components/button";
import Icon from "../../components/icons";
import QRCode from "qrcode.react";
const trackingColor = {
    pending: "#f79e05",
    ordered: "#f79e05",
    packed: "#f79e05",
    shipped: "#f79e05",
    delivered: "#0dbf49",
};

const statusIcon = {
    ordered: (
        <Icon name="user-check" size="2rem" color={trackingColor.ordered} />
    ),
    packed: <Icon name="box" size="2rem" color={trackingColor.packed} />,
    shipped: <Icon name="truck" size="2rem" color={trackingColor.shipped} />,
    delivered: <Icon name="home" size="2rem" color={trackingColor.delivered} />,
};
class Block extends React.PureComponent {
    state = {
        loading: true,
        searchValue: "",
        orders: [],
        order: {},
        showTrack: -1,
        shopId : -1
    };

    async componentDidMount() {
        console.log(this.props.params);
        this.setState({ loading: false });
        await this.getOrders();
    }

    getOrders = async () => {
        this.setState({ loading: true });
        let orderId = this.props.params.orderId;
        try {
            let order = await call.user("/users/order?id=" + orderId, {
                headers: {
                    authorization: this.props.token,
                },
            });
            let result = await call.user(
                "/users/order/products?orderId=" + orderId,
                {
                    headers: {
                        authorization: this.props.token,
                    },
                }
            );
            console.log("------------------>", result.data);
            this.mapOrders(result.data.orderProducts, {});
            this.setState({ loading: false, order: order.data.order });
        } catch (error) {
            this.setState({ loading: false });
        }
    };

    mapOrders = (orders, order) => {
        let products = orders;
        let tempSet = new Set();
        let list = {};
        products.forEach((value, index) => {
            if (!tempSet.has(value.sellerId)) {
                list[value.sellerId] = {
                    sellerId: value.sellerId,
                    shopName: value.shopName,
                    shopAddress: value.shopAddress,
                    sellerPhoneNumber: value.sellerPhoneNumber,
                    products: [],
                };
                tempSet.add(value.sellerId);
            }

            list[value.sellerId].products.push(value);
        });
        this.setState({ order, orders: list });
    };

    render() {
        let trackBox = (shop, id) =>
            this.state.orders[shop].products[id].tracking.map((item, index) => {
                if (index === 0) {
                    return (
                        <div className={styles.trackBox}>
                            <span className={styles.trackCircle}></span>
                            <span className={styles.gap}></span>
                            <div className={styles.trackBarBox}>
                                <div className={styles.trackTextBox}>
                                    <h6>
                                        {statusIcon[item.status.toLowerCase()]}
                                        <span className={styles.gap}></span>
                                        {item.status +
                                            ", \t" +
                                            item.time +
                                            ", " +
                                            item.date +
                                            ", " +
                                            item.address}
                                    </h6>
                                </div>
                            </div>
                        </div>
                    );
                } else {
                    return (
                        <div className={styles.trackBox}>
                            <span className={styles.trackCircle}></span>
                            <span className={styles.trackBar}></span>
                            <span className={styles.gap}></span>
                            <div className={styles.trackBarBox}>
                                <div className={styles.trackTextBox}>
                                    <h6>
                                        {statusIcon[item.status.toLowerCase()]}
                                        <span className={styles.gap}></span>

                                        {item.status +
                                            ", \t" +
                                            item.time +
                                            ", " +
                                            item.date +
                                            ", " +
                                            item.address}
                                    </h6>
                                </div>
                            </div>
                        </div>
                    );
                }
            });

        let orderBlock = Object.values(this.state.orders).map(
            (value, index) => {
                let address = value.sellerAddress;
                let ad = address
                    ? `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`
                    : "";
                // if (address) {
                //     address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`;
                // }
                return (
                    <div className={styles.borderBox}>
                        <HeadingReg
                            title={value.shopName + "  #" + value.sellerId}
                        />
                        <br></br>
                        <div className={styles.displayFlexRow}>
                            <p>Shop Phone Number {value.sellerPhoneNumber}</p>
                            <p>Shop Address {ad}</p>
                        </div>
                        <br></br>
                        {value.products.map((product, index) => {
                            return (
                                <div
                                    className={styles.displayFlexRow}
                                    style={{
                                        borderBottom: "1px solid #eee",
                                        margin: "0.5rem 0",
                                        flexWrap: "wrap",
                                    }}
                                >
                                    <div style={{ width: "20rem" }}>
                                        <img
                                            src={
                                                baseURL +
                                                "/images/" +
                                                product.image
                                            }
                                            width="90px"
                                        />
                                    </div>
                                    <div style={{ flex: 10 }}>
                                        <div>
                                            <HeadingReg
                                                title={
                                                    "Tracking ID #" +
                                                    product._id
                                                }
                                            />
                                            <HeadingSmall
                                                title={product.name}
                                            />
                                            <HeadingSmall
                                                title={`Rs ${product.amount} ( Rs ${product.mrp} per unit)`}
                                            />
                                            <HeadingSmall
                                                title={`Quantity ${product.quantity} `}
                                            />
                                        </div>
                                    </div>
                                  
                                    <div style={{ flex: 3 }}>
                                        <HeadingSmall
                                            title={`Status : ${product.status}`}
                                        />
                                    </div>
                                    <div style={{ flex: 3 }}>
                                        {this.state.showTrack === index &&
                                        this.state.shopId === value.sellerId ? (
                                            <ButtonTwo
                                                title="Close"
                                                onClick={() =>
                                                    this.setState({
                                                        showTrack: -1,
                                                        shopId: -1,
                                                    })
                                                }
                                            />
                                        ) : (
                                            <ButtonTwo
                                                title="Track Your Product"
                                                onClick={() =>
                                                    this.setState({
                                                        showTrack: index,
                                                        shopId: value.sellerId,
                                                    })
                                                }
                                            />
                                        )}
                                    </div>
                                    {this.state.showTrack === index &&
                                    this.state.shopId === value.sellerId ? (
                                        <div
                                            style={{
                                                width: "90%",
                                                position: "relative",
                                                padding: "1rem",
                                                margin: "2rem 2rem",
                                            }}
                                        >
                                            {trackBox(value.sellerId, index)}
                                        </div>
                                    ) : null}
                                </div>
                            );
                        })}
                    </div>
                );
            }
        );
        let order = this.state.order;
        let address = "";
        if (order.receiverAddress) {
            address = order.receiverAddress;
            address = `${address.address}, ${address.city}, ${address.state}, ${address.pincode}`;
        }
        let pinIndex = this.props.user.ordersList.findIndex((value) => {
            return value.orderId === order.orderId
        })
        let pin = pinIndex >= 0 && this.props.user && this.props.user.ordersList ? this.props.user.ordersList[pinIndex].pin : ''
        // console.log("Pinnnn",pin);
        return (
            <div className={styles.container}>
                {this.state.loading ? <Loader /> : null}
                {/* <HeadingMed title="Your Orders" /> */}
                <div className={styles.borderBox}>
                    <HeadingReg title="Delivery Details" />
                    <div className={styles.displayFlexRow}>
                        <div>
                            <HeadingSmall
                                title={"Receiver Name - " + order.receiverName}
                            />
                            <HeadingSmall
                                title={"Delivery Address - " + address}
                            />
                            <HeadingSmall
                                title={
                                    "Receiver Phone Number - " +
                                    order.receiverPhoneNumber
                                }
                            />
                            <HeadingSmall title={"Pin - " + pin} />
                        </div>
                        <div>
                            <QRCode value={`{"orderId" : "${order.orderId ? order.orderId : null}", "pin" : "${pin ? pin : null}"}`} />
                        </div>
                        <div>
                            <HeadingSmall
                                title={"Payment Method - " + order.method}
                            />
                            {order.transactionId ? (
                                <HeadingSmall
                                    title={
                                        "Transaction ID  #" +
                                        order.transactionId
                                    }
                                />
                            ) : null}
                            <HeadingSmall
                                title={
                                    "Payment Status - " + order.paymentStatus
                                        ? "Paid"
                                        : "Not Paid"
                                }
                            />
                            <HeadingSmall
                                title={"Total Amount - Rs " + order.amount}
                            />
                        </div>
                    </div>
                </div>
                <div className={styles.shopBlock}>{orderBlock}</div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps)(Block);
