import React, { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import classes from "./style.css";
import fire from "../../../fire";
import { colors, images } from "../../../constants";
import call from "../../../api/call";
import OtpInput from "react-otp-input";
import Icon from "../../../components/icon";
import Spinner from "../../components/loaders/spinner";
import { ButtonOne } from "../../components/button";
import { useHistory } from "react-router-dom";
import { loadUser } from "../../../actions";
import { HeadingReg } from "../../components/headings";
const Routes = (props) => {
    const [phoneNumber, changePhoneNumber] = useState("");
    const [spinner, changeSpinner] = useState(false);
    const [loading, changeLoading] = useState(true);
    const [otp, changeOtp] = useState("");
    const [showOtpBlock, changeOtpBlock] = useState(false);
    const [confirm, changeConfirm] = useState(null);

    useEffect(async () => {
        changeLoading(false);
        await phoneVerified();
        // console.log("Prosp +++ ",props);
    }, []);

    // let history = useHistory();

    const getOtp = () => {
        changeSpinner(true);
        let rc = new fire.auth.RecaptchaVerifier("rc");
        fire.auth()
            .signInWithPhoneNumber("+91" + phoneNumber, rc)
            .then((data) => {
                changeConfirm(data);
                changeOtpBlock(true);
                changeSpinner(false);
            })
            .catch((err) => {
                console.log(err);
                changeSpinner(false);
            });
    };

    const phoneVerified = async () => {
        try {
            changeLoading(true);
            let phoneNumber = fire.auth().currentUser.phoneNumber;
            let uid = fire.auth().currentUser.uid;
            if (uid) {
                let result = await call.user("/users/login", {
                    data: {
                        phoneNumber,
                        uid,
                    },
                });
                let token = "Bearer " + result.data.token;
                props.loadUser({ token, user: result.data.user });
                localStorage.setItem("token", token);
                changeLoading(false);
                props.history.goBack();
            }
        } catch (error) {
            changeLoading(false);
            if (error.response && error.response.status === 404) {
                props.history.push("/register");
            }
        }
    };

    const verifyOtp = () => {
        changeSpinner(true);
        confirm
            .confirm(otp)
            .then(async (res) => {
                let result = await call.user("/users/login", {
                    data: {
                        phoneNumber: res.user.phoneNumber,
                        uid: res.user.uid,
                    },
                });
                console.log("===============>", result.data.user);
                let token = "Bearer " + result.data.token;
                localStorage.setItem("token", token);
                props.loadUser({
                    token,
                    user: result.data.user,
                });
                changeSpinner(false);
                props.history.goBack();
            })
            .catch((err) => {
                if (err.response && err.response.status === 404) {
                    props.history.push("/register");
                }
                changeSpinner(false);
            });
    };
    return (
        <div className={classes.container}>
            <img src={images.BG_3} className={classes.leftImg} />
            <img src={images.logo} className={classes.leftCorner} />
            <div className={classes.left}>
                <img src={showOtpBlock ? images.BG_2 : images.BG_1} />
                <h2 style={{ textAlign: "center", margin: "0" }}>
                    Get Things Fast
                </h2>
                <h5 style={{ textAlign: "center", margin: "0" }}>
                    Shop Online.Fast Delivery
                </h5>
            </div>
            <div className={classes.right}>
                <div className={classes.rightBox}>
                    <h2>Welcome Back </h2>
                    {!showOtpBlock ? (
                        <div className={classes.box}>
                            <h3>Login</h3>
                            <h5>Please enter your Phone Number</h5>
                            <div className={`${classes.phoneInput}`}>
                                <span> +91</span>
                                <input
                                    type="text"
                                    maxLength={10}
                                    onChange={({ target }) =>
                                        changePhoneNumber(target.value)
                                    }
                                />
                            </div>
                            <div className={classes.captcha} id="rc"></div>
                            <ButtonOne
                                loading={spinner}
                                title="Get OTP"
                                onClick={getOtp}
                            />
                            <div className={classes.displayCenter}></div>
                            <h5
                                style={{
                                    textAlign: "center",
                                    marginTop: "5rem",
                                }}
                            >
                                Connect us on
                            </h5>
                            <div>
                                <Icon name="instagram" />
                                <Icon name="facebook" />
                                <Icon name="twitter" />
                                <Icon name="gmail" />
                                <Icon name="whatsapp" />
                            </div>
                        </div>
                    ) : (
                        <div className={classes.box}>
                            {/* <div className={classes.hr}></div> */}
                            <h3>Verify OTP</h3>
                            <h5>
                                We have send the OTP to number +91{phoneNumber}
                            </h5>
                            <OtpInput
                                numInputs={6}
                                isInputNum={true}
                                isInputSecure={false}
                                // className={classes.inputClass}
                                inputStyle={classes.otpInput}
                                containerStyle={classes.inputClass}
                                value={otp}
                                onChange={(otp) => changeOtp(otp)}
                            />
                            <ButtonOne
                                loading={spinner}
                                title="Verify OTP"
                                onClick={verifyOtp}
                            />
                        </div>
                    )}
                </div>
            </div>
            <h6 className={classes.footer}>
                Made with
                <Icon name="heart" color={colors.CORAL_RED} size="1.5rem" />
                in India
            </h6>
            <div className={classes.buttonBox}>
                <button className={classes.roundButton} onClick={() => props.history.push('/')}>Home</button>
                <button className={classes.roundButton}>Contact Us</button>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Routes);
