import React from "react";
import styles from "./style.css";
import Logo from "../../assets/images/logo-2.png";
import { images, categories } from "../../constants";
import Icom from "../icon";
import Icon from "../icon";
import { ButtonOne } from "../button";
import OtpInput from "react-otp-input";
import fire from "../../fire";
import Loader from "../loaders/loaders";
import call from "../../api/call";
import { loadUser } from "../../actions";
import { connect } from "react-redux";
let fields = [
    "name",
    "email",
    "address",
    "city",
    "state",
    "pincode",
    "landmark",
];
let fieldText = [
    "Name",
    "Email",
    "Address",
    "City",
    "State",
    "Pincode",
    "Landmark",
];
let fieldsIcon = [
    "user",
    "at-sign",
    "home",
    "truck",
    "map-pin",
    "map-pin",
    "map-pin",
];
class Header extends React.PureComponent {
    state = {
        uid: "",
        phoneNumber: "",
        name: "",
        city: "",
        state: "",
        address: "",
        pincode: "",
        email: "",
        landmark: "",
        lat: 0.0,
        lng: 0.0,
        spinning: false,
        loading: true,
    };
    componentDidMount() {
        // alert(fire.auth().currentUser.phoneNumber)
        this.setState({
            uid: fire.auth().currentUser.uid,
            phoneNumber: fire.auth().currentUser.phoneNumber,
            spinning: false,
        });
        this.getLocation();
    }

    getLocation = () => {
        this.setState({ loading: true });
        navigator.geolocation.getCurrentPosition(
            (position) => {
                this.setState({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                    loading: false,
                });
            },
            (error) => {
                this.setState({ loading: false });
                alert("Please allow location");
                this.getLocation();
            },
            { enableHighAccuracy: true }
        );
    };

    inputHandler = (event, idx) => {
        let newState = { ...this.state };
        newState[fields[idx]] = event.target.value;
        this.setState({
            ...newState,
        });
    };
    registerHandler = async () => {
        this.setState({ spinning: true });
        let {
            name,
            phoneNumber,
            email,
            uid,
            state,
            city,
            pincode,
            address,
            landmark,
            lat,
            lng,
        } = { ...this.state };

        let data = {
            name,
            phoneNumber,
            email,
            uid,
            address: {
                address,
                city,
                state,
                pincode,
                landmark,
            },
            location: {
                type: "Point",
                coordinates: [lng, lat],
            },
        };
        try {
            let result = await call.user("/users/register", {
                data,
            });
            let token = "Bearer " + result.data.token;
            this.props.loadUser({ token, user: result.data.user });
            localStorage.setItem("token", token);
            this.setState({ spinning: false });
            this.props.close();
        } catch (error) {
            alert(error);
            this.setState({ spinning: false });
        }
    };

    render() {
        let fieldBlock = fields.map((value, index) => {
            return (
                <div className={`${styles.inputFieldBlock}`}>
                    <span className={styles.phoneCodeBlock}>
                        <Icon name={fieldsIcon[index]} />
                    </span>
                    <input
                        placeholder={fieldText[index]}
                        type="text"
                        onChange={(event) => this.inputHandler(event, index)}
                        value={this.state[value]}
                    />
                </div>
            );
        });
        return (
            <div className={`${styles.authBlock}`}>
                {this.state.loading ? <Loader /> : null}
                <div className={`displayFlexCol ${styles.leftBox}`}>
                    <div className={styles.leftBoxImage}>
                        <img src={images.BG_3} className={styles.cover} />

                        <img src={images.BG_2} />
                        <div className={styles.rightBoxHeading}>
                            <h2 style={{ textAlign: "center" }}>
                                Get Amazing Deals
                            </h2>
                            <p style={{ textAlign: "center" }}>
                                Shop Here.More Offers
                            </p>
                        </div>
                    </div>
                </div>

                <div className={`displayFlexCol ${styles.rightBox}`}>
                    <img src={images.BG_4} className={styles.coverRight} />
                    <div className={styles.rightBoxHeading}>
                        <h2>Register</h2>
                        <p>
                            Please enter your details {this.state.phoneNumber}
                        </p>
                    </div>
                    <p
                        className={styles.button}
                        onClick={() => { fire.auth().signOut();this.props.changeScreen("LOGIN")}}
                    >
                        Edit Phone Number
                    </p>
                    {fieldBlock}
                    <div
                        className={`${styles.displayFlexCol}`}
                        style={{ marginTop: "2rem" }}
                    >
                        <ButtonOne
                            title="Register"
                            loading={this.state.spinning}
                            onClick={this.registerHandler}
                        />
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Header);
