import React, { useEffect, useState } from "react";
import styles from "./style.css";
import Logo from "../../assets/images/logo-2.png";
import { images, categories } from "../../constants";
import { loadUser } from "../../actions";
import Icon from "../icon";
import { ButtonOne } from "../button";
import OtpInput from "react-otp-input";
import fire from "../../fire";
import Loader from "../../components/loaders/loaders";
import Spinner from "../../components/loaders/spinner";
import call from "../../api/call";
import { connect } from "react-redux";
const Header = (props) => {
    const [phoneNumber, changePhoneNumber] = useState("");
    const [spinner, changeSpinner] = useState(false);
    const [loading, changeLoading] = useState(true);
    const [otp, changeOtp] = useState("");
    const [showOtpBlock, changeOtpBlock] = useState(false);
    const [confirm, changeConfirm] = useState(null);
    
    useEffect(async () => {
        changeLoading(false);
        await phoneVerified();
    }, []);
    const getOtp = () => {
        changeSpinner(true);
        let rc = new fire.auth.RecaptchaVerifier("rc");
        fire.auth()
            .signInWithPhoneNumber("+91" + phoneNumber, rc)
            .then((data) => {
                changeConfirm(data);
                changeOtpBlock(true);
                changeSpinner(false);
            })
            .catch((err) => {
                console.log(err);
                changeSpinner(false);
            });
    };

    const phoneVerified = async () => {
        try {
            changeLoading(true);
            let phoneNumber = fire.auth().currentUser.phoneNumber;
            let uid = fire.auth().currentUser.uid;
            if (uid) {
                let result = await call.user("/users/login", {
                    data: {
                        phoneNumber,
                        uid,
                    },
                });
                let token = "Bearer " + result.data.token;
                props.loadUser({ token, user: result.data.user });
                localStorage.setItem("token", token);
                changeLoading(false);
                props.close();
            }
        } catch (error) {
            changeLoading(false);
            if (error.response && error.response.status === 404) {
                props.changeScreen("REGISTER");
            }
        }
    };

    const verifyOtp = () => {
        changeSpinner(true);
        confirm
            .confirm(otp)
            .then(async (res) => {
                let result = await call.user("/users/login", {
                    data: {
                        phoneNumber: res.user.phoneNumber,
                        uid: res.user.uid,
                    },
                });
                console.log("===============>", result.data.user);
                let token = "Bearer " + result.data.token;
                localStorage.setItem("token", token);
                props.loadUser({
                    token,
                    user: result.data.user,
                });
                changeSpinner(false);
                props.close();
            })
            .catch((err) => {
                if (err.response.status === 404) {
                    props.changeScreen("REGISTER");
                }
                changeSpinner(false);
            });
    };
    return loading ? (
        <Loader />
    ) : (
        <div className={`${styles.authBlock}`}>
            <div className={`displayFlexCol ${styles.leftBox}`}>
                <img src={images.BG_3} className={styles.cover} />
                <div className={styles.leftBoxImage}>
                    <img src={images.BG_1} />
                    <div className={styles.rightBoxHeading}>
                        <h2 style={{ textAlign: "center" }}>Get Things Fast</h2>
                        <p style={{ textAlign: "center" }}>
                            Shop Online.Fast Delivery
                        </p>
                    </div>
                </div>
            </div>

            <div className={`displayFlexCol ${styles.rightBox}`}>
                <img src={images.BG_4} className={styles.coverRight} />
                {!showOtpBlock ? (
                    <div style={{ paddingTop: "30%" }}>
                        <div className={styles.rightBoxHeading}>
                            <h2>Login</h2>
                            <p>Please enter your Phone Number</p>
                        </div>
                        <div
                            className={`displayFlexRow ${styles.inputFieldBlock}`}
                        >
                            <span className={styles.phoneCodeBlock}>+91</span>
                            <input
                                placeholder="Phone Number"
                                type="text"
                                maxLength={10}
                                onChange={({ target }) => {
                                    changePhoneNumber(target.value);
                                }}
                            />

                            <div
                                style={{
                                    width: "100%",
                                    backgroundColor: "#ddd",
                                    height: "1px",
                                    marginTop: "1rem",
                                }}
                            ></div>
                            <div
                                id="rc"
                                style={{
                                    marginTop: "1rem",
                                    marginBottom: "1rem",
                                }}
                            ></div>
                            <div
                                className={`${styles.displayFlexCol}`}
                                style={{ marginTop: "2rem" }}
                            >
                                <ButtonOne
                                    title="Get OTP"
                                    onClick={getOtp}
                                    loading={spinner}
                                    rightIcon="arrow-right"
                                />
                            </div>
                        </div>
                    </div>
                ) : (
                    <div style={{ paddingTop: "30%" }}>
                        <div className={styles.rightBoxHeading}>
                            <h2>Verify Account</h2>
                            <p>We have send OTP at {phoneNumber}</p>
                        </div>
                        <div
                            className={`displayFlexRow ${styles.inputFieldBlock}`}
                        >
                            <OtpInput
                                numInputs={6}
                                isInputNum={true}
                                isInputSecure={false}
                                className={styles.inputClass}
                                inputStyle={styles.otpInput}
                                value={otp}
                                onChange={(otp) => changeOtp(otp)}
                            />

                            <p
                                className={styles.button}
                                onClick={() => changeOtpBlock(false)}
                            >
                                Edit Phone Number
                            </p>
                            <div
                                style={{
                                    width: "100%",
                                    backgroundColor: "#ddd",
                                    height: "1px",
                                    marginTop: "1rem",
                                }}
                            ></div>
                            <div
                                className={`${styles.displayFlexCol}`}
                                style={{ marginTop: "2rem" }}
                            >
                                <ButtonOne
                                    title="Verify OTP"
                                    onClick={verifyOtp}
                                    loading={spinner}
                                />
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

let mapStateToProps = (state) => {
    return state;
};
export default connect(mapStateToProps, {
    loadUser,
})(Header);
