import React, { useEffect, useState } from "react";
import styles from "./style.css";
import Logo from "../../assets/images/logo-2.png";
import { images, categories } from "../../constants";
import Icon from "../../components/icon";
import AuthScreen from "../auth";
import { ButtonOne, RoundButton } from "../../components/button";
import { connect } from "react-redux";
import { loadUser } from "../../actions";
import Loader from "../loaders/loaders";
import fire from "../../fire";
const Header = (props) => {
    let [showAuth, changeAuthBlock] = useState({
        type: false,
        routeName: "LOGIN",
    });
    const [searchValue, changeSearchValue] = useState("");
    const [loading, changeLoading] = useState(false);
    let categoriesBlock = categories.map((value, index) => {
        return (
            <div
                className={`${styles.displayFlexCol} ${styles.categoriesItem}`}
            >
                <img src={images["CT_" + index]} />
                <li>{value}</li>
            </div>
        );
    });
    useEffect(() => {
        changeSearchValue(props.searchValue);
    }, [props.searchValue]);

    return (
        <header className={styles.header}>
            {showAuth.type ? (
                <AuthScreen
                    close={() => changeAuthBlock(false)}
                    routeName={showAuth.routeName}
                />
            ) : null}
            {loading ? <Loader /> : null}
            <div>
                <div className={styles.displayFlexRow}>
                    <div className={styles.logo}>
                        <img
                            alt="logo"
                            src={Logo}
                            onClick={() => props.history.push("/")}
                        />
                    </div>
                    {props.title === "HOME" ||
                    props.title === "SEARCH_SCREEN" ? (
                        <div className={styles.searchBox}>
                            <input
                                type="text"
                                placeholder="Search Products"
                                value={searchValue}
                                onChange={({ target }) => {
                                    changeSearchValue(target.value);
                                }}
                                onKeyPress={({ code }) => {
                                    if (code === "Enter") {
                                        props.history.push(
                                            "/search?name=" + searchValue
                                        );
                                    }
                                }}
                            />
                            <div className={styles.rightIcon}>
                                <Icon
                                    name="search"
                                    size="2.5rem"
                                    onClick={() => {
                                        props.history.push(
                                            "/search?name=" + searchValue
                                        );
                                    }}
                                />
                            </div>
                        </div>
                    ) : null}
                    <div
                        className={`${styles.rightTabContainer} ${styles.displayFlexRow}`}
                    >
                        <img src={images["APP-STORE"]} />
                        <img src={images["PLAY-STORE-ANDROID"]} />
                        {props.user.name ? (
                            <div
                                className={`${styles.userBlock} ${styles.displayFlexRow}`}
                            >
                                {props.title !== "HOME" ? (
                                    <Icon
                                        name="home"
                                        size="3rem"
                                        onClick={() => {
                                            props.history.push("/");
                                        }}
                                    />
                                ) : null}
                                <div className={styles.userIcon}>
                                    <Icon
                                        name="user"
                                        size="3rem"
                                        onClick={() => {
                                            props.history.push("/account");
                                        }}
                                    />
                                </div>

                                {/* <div style={{ margin: "0 1rem" }}></div> */}
                                <div className={styles.logoutButton}>
                                    <RoundButton
                                        title="Logout"
                                        onClick={() => {
                                            changeLoading(true);
                                            props.loadUser({
                                                token: "",
                                                user: { userCartItems: [] },
                                            });
                                            localStorage.removeItem("token");
                                            fire.auth().signOut();
                                            changeLoading(false);
                                        }}
                                    />
                                </div>

                                <Icon
                                    name="bell"
                                    size="3rem"
                                    onClick={() => {
                                        props.history.push("/account");
                                    }}
                                />
                            </div>
                        ) : (
                            <ButtonOne
                                title="Login"
                                onClick={() => {
                                    changeAuthBlock({
                                        type: true,
                                        routeName: "LOGIN",
                                    });
                                }}
                            />
                        )}

                        <div className={`${styles.displayFlexRow}`}>
                            <div className={styles.cart}>
                                <Icon name="shopping-cart" size="3.5rem" />
                                {!props.user ||
                                props.user.userCartItems.length === 0 ? null : (
                                    <h4 className={styles.cartValueBox}>
                                        {props.user.userCartItems.length}
                                    </h4>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {!props.hide ? (
                <div className={styles.categoriesBox}>
                    <ul className={styles.categoriesList}>{categoriesBlock}</ul>
                </div>
            ) : null}
        </header>
    );
};
const mapStateToProps = (state) => {
    return state;
};
export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Header);
