import React, { useEffect, useState } from "react";
import classes from "./style.css";
import {} from "react-router-dom";
import { connect } from "react-redux";
import { HeadingMed } from "../../../components/headings";
import { colors, images } from "../../../constants";
import Icon from "../../components/icons";
import { HeadingSmall, HeadingReg } from "../../components/headings";
import { ButtonTwo } from "../../components/button";
import { baseURL } from "../../../api";
import call from "../../../api/call";
import Spinner from "../../components/loaders/spinner";
import { loadUser } from "../../../actions";
const Routes = (props) => {
    const [products, changeProducts] = useState([]);
    const [loading, changeLoading] = useState(false);
    const [paymentOption, changePaymentOption] = useState("COD");
    const [price, changePrice] = useState({ amount: 0, discount: 0, price: 0 });
    useEffect(() => {
        changeProducts(props.location.state.products);
        let amount = 0;
        let discount = 0;
        let price = 0;
        props.location.state.products.forEach((item) => {
            amount += item.amount * item.quantity;
            price += item.mrp * item.quantity;
            discount += item.discount * item.quantity;
        });
        amount = amount.toFixed(2);
        price = price.toFixed(2);
        discount = discount.toFixed(2);
        changePrice({ amount, discount, price });
    }, [props.location]);

    const checkPayment = async (receipt, order) => {
        console.log("REceipt --->", receipt, order);
    };
    const orderHandler = async (sellerCartId) => {
        changeLoading(true);
        let byCart = props.location.state.from === "CART" ? true : false;
        let details = {
            address: props.user.address[0],
            name: props.user.name,
            sellerCartId,
        };
        try {
            let result = {};
            if (byCart) {
                result = await call.user("/users/order/by-cart", {
                    data: {
                        method: paymentOption,
                        ...details,
                    },
                    headers: {
                        authorization: props.token,
                    },
                });
            } else {
                result = await call.user("/users/order", {
                    data: {
                        method: paymentOption,
                        quantity: products[0].quantity,
                        ...details,
                    },
                    headers: {
                        authorization: props.token,
                    },
                });
            }
            let user = await call.user("/users", {
                headers: {
                    authorization: props.token,
                },
            });
            props.loadUser({ user: user.data.user });
            changeLoading(false);
            if (paymentOption !== "COD") {
                await paymentHandler(result.data.receipt, result.data.order);
            } else {
                props.history.push("/payment/success");
            }
            // Alert.alert('Congratulations' , "Your Order has been placed!!")
        } catch (error) {
            console.log(error);
            changeLoading(false);
                   props.history.push("/payment/fail");
        }
    };

    const paymentHandler = async (receipt, order) => {
        var options = {
            key: "rzp_test_F7dJtxqCBiBD7R", // Enter the Key ID generated from the Dashboard
            amount: parseInt(price.amount * 100), // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
            currency: "INR",
            name: props.user.name,
            description: "Test Transaction",
            image: baseURL + "/logo.png",
            order_id: receipt.id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
            handler: async (response) => {
                // await checkPayment(response,order);
                try {
                    let result = await call.user("/users/order/payment", {
                        headers: {
                            authorization: props.token,
                        },
                        data: {
                            ...response,
                            orderId: order.orderId,
                        },
                    });
                    console.log("payment reul==>", result.data);
                    // alert("success");
                    props.history.push("/payment/success");
                } catch (error) {
                    // alert("failed");
                    // console.log("Payment ===> ", error);
                    props.history.push("/payment/fail");
                }
            },
            prefill: {
                name: props.user.name,
                email: props.user.email,
                contact: props.user.phoneNumber,
            },
            notes: {
                address: "Razorpay Corporate Office",
            },
            theme: {
                color: colors.AQUA_BLUE,
            },
        };
        var rzp1 = new window.Razorpay(options);
        rzp1.on("payment.failed", (response) => {
            props.history.push("/payment/fail");
        });
        rzp1.open();
    };

    let address = props.user ? props.user.address[0] : null;
    let add = address
        ? `${address.address}, ${address.city}, ${address.state},Near - ${address.landmark}, ${address.pincode}`
        : "";
    let productsBlock = products.map((item, idx) => {
        return (
            <div
                key={item.name}
                className={classes.listBlock}
                onClick={() => {
                    props.history.push("/product?sellerCartId=" + item._id);
                }}
            >
                <div className={classes.imageContainer}>
                    {item.image ? (
                        <img
                            src={baseURL + "/images/" + item.image}
                            className={classes.listImg}
                        />
                    ) : (
                        <img
                            src={baseURL + "/images/" + item.images[0]}
                            className={classes.listImg}
                        />
                    )}
                </div>
                <div className={classes.itemBox}>
                    <HeadingReg title={idx + 1 + " . " + item.name} />
                    <h6>Quantity {item.quantity}</h6>
                    <h6>
                        Amount Rs {(item.amount * item.quantity).toFixed(2)}
                    </h6>
                </div>
            </div>
        );
    });
    return (
        <div className={classes.container}>
            {loading ? <Spinner /> : null}
            <div className={classes.displayFlexRow}>
                <div className={classes.leftBox}>
                    <img src={images.BG_7} className={classes.bgImg} />
                </div>
                <div className={classes.rightBox}>
                    <div style={{ width: "100%" }}>
                        <HeadingMed title="Order Summary" />
                    </div>
                    <div className={classes.listItems}>{productsBlock}</div>
                    <div className={classes.orderDetails}>
                        <div className={classes.userDetails}>
                            <div className={classes.displayFlexRowEqual}>
                                <Icon name="user" color={colors.CORAL_RED} />
                                <h6>{props.user.name}</h6>
                            </div>
                            <div className={classes.displayFlexRowEqual}>
                                <Icon name="user" color={colors.CORAL_RED} />
                                <h6>{props.user.phoneNumber}</h6>
                            </div>
                            <div className={classes.addressBox}>
                                <h6>{add}</h6>
                            </div>
                        </div>
                        <HeadingReg title="Price Details" />
                        <div className={classes.displayFlexRow}>
                            <p>Total Items</p>
                            <p>{products.length}</p>
                        </div>
                        <div className={classes.displayFlexRow}>
                            <p>Price</p>
                            <p>Rs {price.price}</p>
                        </div>
                        <div className={classes.displayFlexRow}>
                            <p>Discount</p>
                            <p>Rs {price.discount}</p>
                        </div>
                        <div className={classes.hr}></div>
                        <div className={classes.displayFlexRow}>
                            <p>Amount Payable</p>
                            <p>Rs {price.amount}</p>
                        </div>
                        <span className={classes.gap}></span>
                        <HeadingReg title="Payment Option" />
                        <div
                            className={`${classes.displayFlexRow} ${classes.borderBox}`}
                        >
                            <HeadingSmall title="Cash on Delivery" />
                            <input
                                type="radio"
                                checked={paymentOption === "COD"}
                                onClick={() => {
                                    changePaymentOption("COD");
                                }}
                                className={classes.checkBox}
                                name="payment"
                            />
                        </div>
                        <div
                            className={`${classes.displayFlexRow} ${classes.borderBox}`}
                        >
                            <HeadingSmall title="Online - RazorPay" />
                            <input
                                type="radio"
                                checked={paymentOption === "ONLINE"}
                                onClick={() => {
                                    changePaymentOption("ONLINE");
                                }}
                                className={classes.checkBox}
                                name="payment"
                            />
                        </div>
                        <div className={classes.bottomBox}>
                            <HeadingMed title={"Rs " + price.amount} />
                            <ButtonTwo
                                title="Continue"
                                onClick={() => orderHandler(products[0]._id)}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps, {
    loadUser: loadUser,
})(Routes);
