import React from "react";
import styles from "./style.css";
import sprite from "../../assets/icons/sprite.svg";
import * as Loader from "react-spinners";
import Icon from "../icon";
import { motion } from "framer-motion";
const ButtonOne = (props) => {
    return (
        <button className={styles.buttonOne} {...props}>
            {props.leftIcon}
            {props.title}{" "}
            {props.rightIcon ? <Icon name={props.rightIcon} /> : null}
            {"  "}
            {props.loading ? (
                <span style={{ marginLeft: "1rem" }}>
                    <Loader.SyncLoader size={10} color="#fff" />
                </span>
            ) : null}
        </button>
    );
};

const RoundButton = (props) => {
    return (
        <motion.button className={styles.roundButton} {...props}>
            {props.leftIcon}
            {props.title}{" "}
            {props.rightIcon ? <Icon name={props.rightIcon} /> : null}
        </motion.button>
    );
};

export { ButtonOne, RoundButton };
