import * as products from './productCalls';
import call from './call';

const baseURL = `https://quiet-scrubland-22380.herokuapp.com`;

const fetchUser = async(token) => {
    let result = await call.user('/users/', {
        headers : {
            authorization : token
        }
    })

    return result.data.user;
}


export {products,fetchUser,baseURL}