let intialState = {
    user: {},
    token: null,
};

const reducer = (state=intialState, action) => {
    let { payload, type } = { ...action };
    switch (type) {
        case 'LOAD_USER':
            let newState = { ...state, ...payload };
            return newState;
        default : return state
    }
}

export { reducer };