import React, { useEffect, useState } from "react";
import {} from "react-router-dom";
import { connect } from "react-redux";
import { HeadingMed,HeadingReg } from "../../components/headings";
import classes from './style.css';
import call from "../../../api/call";
import Spinner from '../../components/loaders/spinner';
import { baseURL } from "../../../api";
import Icon from "../../components/icons";
import { colors } from "../../../constants";
import {loadUser} from '../../../actions'

const Routes = (props) => {

    const [loading, changeLoading] = useState(true);
    const [cart, changeCart] = useState([]);
     const [price, changePrice] = useState({
         amount: 0,
         discount: 0,
         price: 0,
     });
    useEffect(async() => {
        try {
            changeLoading(true);
            let result = await call.user('/users/cart',{
                headers: {
                    authorization : props.token
                }
            })
            console.log("Result--" , result.data);
            changeCart(result.data.cart);
            changeLoading(false)
        } catch (error) {
            changeLoading(false);
        }
    }, [])
    useEffect(() => {
        let amount = 0;
        let discount = 0;
        let price = 0;
        cart.forEach((item) => {
            amount += item.amount * item.quantity;
            price += item.mrp * item.quantity;
            discount += item.discount * item.quantity;
        });
        amount = amount.toFixed(2);
        price = price.toFixed(2);
        discount = discount.toFixed(2);
        changePrice({ amount, discount, price });
    }, [cart]);

    const deleteHandler = async (id) => {
        try {
            changeLoading(true);
            await call.user('/users/cart', {
                config: {
                    method : "DELETE"
                },
                data: {
                  userCartId : id  
                },
                headers: {
                    authorization : props.token
                }
            })
            let result = await call.user('/users', {
                headers: {
                    authorization : props.token
                }
            })
            props.loadUser({
                user : result.data.user
            })
            let index = -1;
            for (let i = 0; i < cart.length; i++){
                if (cart[i]['_id'] === id) {
                    index = i;
                    break;
                }
            }
            let newCart = [...cart];
            newCart.splice(index, 1);
            changeCart(newCart);
            changeLoading(false);
        } catch (error) {
            console.log(error);
            changeLoading(false);
        }
    }

    let cartBlock = cart.map((item, index) => {
        return (
            <div key={index} className={classes.cartBox}>
                <div className={classes.img}>
                    <img src={baseURL + "/images/" + item.image} />
                </div>
                <div className={classes.detailsBox}>
                    <HeadingReg title={item.name} />
                    <p>Seller {item.sellerId.shopName}</p>
                    <p>Qunatity {item.quantity}</p>
                    <p className={classes.amount}>
                        Rs {(item.amount * item.quantity).toFixed(2)}
                    </p>
                </div>
                <div className={classes.buttonBox}>
                    <Icon name="edit" color={colors.ORANGE} onClick={() => {
                        props.history.push('/product?sellerCartId=' + item.sellerCartId)
                    }}/>
                    <Icon name="clear" color={colors.CORAL_RED} onClick={() => deleteHandler(item._id)} />
                </div>
            </div>
        );
    })
    return (
        <div className={classes.container}>
            {loading ? <Spinner /> : null}
            <HeadingMed title="Your Cart" />
            <div className={classes.box}>
                <div className={classes.leftBox}>
                    {cartBlock}
                </div>
                <div className={classes.rightBox}>
                    <HeadingReg title="Your Cart Summary" />
                    <div className={classes.displayFlexRow}>
                        <p>Total Items</p>
                        <p>{cart.length}</p>
                    </div>
                    <div className={classes.displayFlexRow}>
                        <p>Price</p>
                        <p>Rs {price.price}</p>
                    </div>
                    <div className={classes.displayFlexRow}>
                        <p>Discount</p>
                        <p>Rs {price.discount}</p>
                    </div>
                    {/* <span className={classes.hr}></span> */}
                    <div className={classes.displayFlexRow}>
                        <p>Amount Payable</p>
                        <p>Rs {price.amount}</p>
                    </div>
                    <button className={classes.button} onClick={() => {
                        props.history.push({
                            pathname: '/confirm-order',
                            state: {
                                products: cart,
                                from :'CART'
                            }
                        })
                    }}>Buy Now</button>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return state;
};

export default connect(mapStateToProps, {
    loadUser : loadUser
})(Routes);
